<template lang="html">
  <div class="search-head">
    <div class="search-head-left">
      <img @click="bshow = !bshow" src="../../assets/img/head/add@3x.png" alt="">
      <pop-menu @colse-pop="bshow = false" @click-item="clickItem"
      v-if="bshow"></pop-menu>
    </div>
    <div id="test" class="search-head-cnt">
      <span @click="$router.push('/position')" class="clearfix">
        <img class="fl" src="../../assets/img/head/address@3x.png" alt="">
        <span class="fl">{{current}}</span>
        <img v-if="false" src="../../assets/img/head/arrow_down@3x.png" alt="">
      </span>
    </div>
    <div class="search-head-right" v-if="false">
      <a href="tel:13168822512"><span>回拨号盘界面</span></a>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import minutils from '@/utils/minUtils'
import utils from '@/utils/utils'
import popMenu from './popMenu'
export default {
  data () {
    return {
      bshow: false,
      current: '获取中...'
    }
  },
  mounted () {
    this.initPage()
  },
  components: { popMenu },
  computed: {
    ...mapGetters({location: 'getLocation'})
  },
  methods: {
    initPage () {
      minutils.IndicatorOpen('加载中...')
      // 判断store是否存有当前位置
      console.log(this.location)
      if ('street' in this.location) {
        this.getCurrent(this.location)
      } else {
        utils.getCurrentLocation(this.getCurrent)
      }
    },
    // 获取当前位置
    getCurrent (location) {
      // 将当前位置存放到store
      this.$store.dispatch('setLocation', location)
      this.current = location.street
      // 此处调用父类方法, 在该方法的异步方法中关闭加载动画
      this.$emit('get-current-shop', location)
    },
    clickItem (item) {
      console.log(item)
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
.search-head {
  position: fixed;
  z-index: 99;
  background: @searchbgColor;
  height: 1.22rem;
  width: 100%;
  padding: .5rem .2rem;
  color:@whiteColor;
  .search-head-left {
    text-align: left;
    width: .6rem;
    float: left;
    position: relative;
    img {
      width: .46rem;
      width: .46rem;
    }
  }
  .search-head-cnt {
    line-height: .48rem;
    font-size: .36rem;
    margin: 0 .6rem;
    text-align: center;
    img {
      width: .46rem;
      width: .46rem;
    }
    img, span {
      vertical-align: top;
    }
    >span {
      display: inline-block;
      margin-right: .5rem;
      >span {
        display: inline-block;
        max-width: 4.4rem;
        height: .48rem;
        text-overflow: ellipsis;
        white-space:nowrap;
        overflow: hidden;
      }
    }
  }
  .search-head-right {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: .2rem;
    a {
      color: @whiteColor;
    }
    span {
      font-size: .28rem;
      display: inline-block;
      padding: .14rem .05rem;
      border: 1px solid @whiteColor;
      border-radius: .05rem;
    }
  }
}
</style>



