<template>
  <div class="search-index">
    <search-head @get-current-shop="getCurrentShop"></search-head>
    <search-basic @search-word="searchWord"></search-basic>
    <!-- <search-tab @tab-handle="changeTab" ></search-tab> -->
    <div class="searchScroll">
      <search-list @load-more="loadMore" :data="data"></search-list>
    </div>
    <div class="nodata">
      <no-data v-show="!data.length" top="60%"></no-data>
    </div>
    <tabbar selected="/search"></tabbar>
  </div>
</template>
<script>
import api from '@/api'
import minutils from '@/utils/minUtils'
import noData from '@/components/common/noData'
import searchHead from '@/components/head/searchHead'
import tabbar from '@/components/minCommon/tabbar'
import searchBasic from '@/components/search/searchBasic'
import searchTab from '@/components/search/searchTab'
import searchList from '@/components/search/searchList'
export default {
  components: {searchHead, searchBasic, searchTab, searchList, tabbar, noData},
  data () {
    return {
      pageSize: 14,
      data: [],
      searchObj: { index: 0, page: 0, data: [] },
      location: {},
      searchValue: ''
    }
  },
  methods: {
    loadMore (callback) {
      this.ajaxSearch({locationLon: this.location.lng, locationLat: this.location.lat, pageSize: this.pageSize, pageIndex: ++this.searchObj.index, keyWord: this.searchValue, callback: callback})
    },
    searchWord (value) {
      // console.log(/^[\u4e00-\u9fa5]+$/i.test(value))
      this.searchValue = value
      this.searchObj.index = 0
      minutils.IndicatorOpen('加载中...')
      let data = {'locationLon': this.location.lng, 'locationLat': this.location.lat, 'pageSize': this.pageSize, 'pageIndex': this.searchObj.index, 'keyWord': value}
      api.getUserTags(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.data.userTagsNewList.length > 0) {
          this.searchObj.page = result.data.total
          if (this.searchObj.index === 0) {
            this.searchObj.data = result.data.userTagsNewList
          } else {
            this.searchObj.data = [...this.searchObj.data, ...result.data.userTagsNewList]
          }
          this.data = this.searchObj.data
        } else {
          this.data = []
        }
      })
    },
    getCurrentShop (location) {
      this.location = location
      this.ajaxSearch({locationLon: location.lng, locationLat: location.lat, pageSize: this.pageSize, pageIndex: 0, keyWord: ''})
    },
    // 所有异步请求独立，方便各种方法调用
    ajaxSearch ({locationLon = 0, locationLat = 0, pageSize = 0, pageIndex = 0, keyWord = '', callback = null} = {}) {
      if (this.searchObj.page && (pageSize * pageIndex >= this.searchObj.page)) {
        return
      }
      // console.log(this.searchObj.page, pageIndex)
      minutils.IndicatorOpen('加载中...')
      let data = {'locationLon': locationLon, 'locationLat': locationLat, 'pageSize': pageSize, 'pageIndex': pageIndex, 'keyWord': keyWord, 'callback': callback}
      api.getUserTags(data).then(response => {
        minutils.IndicatorClose()
        // 绑定滚动事件
        typeof callback === 'function' && callback()
        let result = response.body
        if (result.data.userTagsNewList.length > 0) {
          this.searchObj.page = result.data.total
          if (this.searchObj.index === 0) {
            this.searchObj.data = result.data.userTagsNewList
          } else {
            this.searchObj.data = [...this.searchObj.data, ...result.data.userTagsNewList]
          }
          this.data = this.searchObj.data
        } else {
          this.data = []
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
@import '../assets/css/variable.less';
.search-index {
  .search-tab {
    margin-top: .3rem;
  }
  .searchScroll {
    padding-top: 2rem;
  }
  .nodata {
    // width: 100%;
    position: relative;
  }
}
</style>



