<template lang="html">
  <div class="search-basic">
    <div class="">
      <input class="search-input" ref="searchInputTag" type="text" name="" v-model="searchWord" placeholder="找服务">
      <i class="search__icon"></i>
      <!-- <span>{{searchWord}}</span> -->
      <i class="search__delete" @click.stop="clearValue" v-if="showdelete"></i>
    </div>

  </div>
</template>

<script>
export default {
  data () {
    return {
      searchWord: '',
      showdelete: false
    }
  },
  methods: {
    clearValue (e) {
      this.searchWord = ''
      this.showdelete = false
      this.$refs.searchInputTag.focus()
    }
  },
  watch: {
    searchWord: function (val, oldVal) {
      this.showdelete = val
      if (val !== oldVal) {
        this.$emit('search-word', this.searchWord)
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.search-basic {
  position: fixed;
  width: 100%;
  z-index: 9;
  margin-top: 1.22rem;
  padding-bottom: .1rem;
  font-size: .3rem;
  background: @searchbgColor;
  > div {
    margin: 0 .22rem;
    position: relative;
  }
}
.search-input {
  display: block;
  width: 100%;
  height: .68rem;
  font-size: .3rem;
  line-height: .68rem;
  color: @lightColor;
  padding-left: .6rem;
  border-radius: .05rem;
  border: none;
  background: rgba(255, 255 ,255, .6);
}
.search__icon {
  position: absolute;
  left: .1rem;
  top: 50%;
  transform: translateY(-50%);
}
.search__delete {
  position: absolute;
  width: .44rem;
  height: .44rem;
  right: .1rem;
  top: 50%;
  transform: translateY(-50%);
  background: url('../../assets/img/delete@2x.png') no-repeat;
  background-size: .44rem .44rem;
}
</style>



