<template lang="html">

  <div class="search-list clearfix"
    v-infinite-scroll="loadMore"
    :infinite-scroll-disabled="loading"
    :infinite-scroll-distance="10"
    :infinite-scroll-immediate-check="true"
    :style="{paddingBottom: paddingB}"
  >
    <div v-for="item in data" class="search-list-item" @click="call(item)">
      <!--  @click="getListDetail(item)"-->
      <div class="detail-cover" @click.stop="getListDetail(item)"></div>
      <div class="search-list-left" :class="{'woman': parseInt(item.sex) === 2}">
        <!-- <span class="search-list-left__name" v-else-if="!item.userRealName && !item.enterpriseId">{{item.userId}}</span> -->
        <img class="search-list-left__img" v-if="item.style === 2" :src="item.logo || zhanwt">
        <span class="search-list-left__name" v-else>{{item.userName | toFormatName}}</span>
        <span class="search-list-left__vip">{{parseInt(item.style) === 2 ? '企业' : '个人'}}</span>
        <!-- <span class="search-list-left__vip" :class="{'redColor': !!item.isVip, 'borderBRed': !!item.isVip}">VIP</span> -->
      </div>
      <!-- 中间灰线 -->
      <span class="search-list-line"></span>
      <!-- 右侧 -->
      <div class="search-list-right">
        <div class="search-list-right__top clearfix">
          <span class="search-list-right__number fl">{{item.mobile && item.mobile | toFormatMobile}}</span>
          <span class="search-list-right__times fl"><i class="phone__icon"></i>{{item.callBy}}次</span>
          <span class="search-list-right__percent fl"><i class="zan-icon"></i>{{item.praiseRate}}%</span>
        </div>
        <div class="search-list-right__middle">
          <span class="search-list-right__address">{{item.mobilePlace}}</span>
          <!-- <span class="search-list-right__address" v-else="item.mobilePlace">{{item.address}}</span> -->
          <i class="arrow__icon"  v-if="isShow"></i>
        </div>
        <div class="search-list-right__bottom clearfix">
          <span class="search-list-right__info fl">{{item.tagName}}</span>
          <span class="search-list-right__range fr" v-if="isShow">{{item.distance | toFormatDistance}}</span>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import _ from 'underscore'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import hzl from '@/assets/img/hzl@2x.png'
export default {
  props: {
    data: {
      type: Array,
      default: []
    },
    isShow: {
      type: Boolean,
      default: true
    },
    paddingB: {
      type: String | Number,
      default: '1.12rem'
    }
  },
  data () {
    return {
      loading: false,
      zhanwt: hzl
    }
  },
  computed: {
    distance: function (data) {
      return typeof parseInt(data) === 'number' ? parseInt(data) + 'km' : data
    },
    userId: function () {
      return utils.userId()
    }
  },
  methods: {
    getListDetail (item) {
      sessionStorage.setItem('searchId', item.userId)
      this.$router.push('/detail')
    },
    loadMore: _.throttle(function () {
      // 关闭无线滚动
      this.loading = true
      this.$emit('load-more', this.changeLoading)
    }, 500),
    // 启动无线滚动
    changeLoading () {
      this.loading = false
    },
    call (item) {
      let self = this
      minutils.MessageBoxConfirm({
        message: '确认拨打商户电话？',
        title: '提示',
        options: {
          confirmButtonText: '确认',
          cancelButtonText: '取消'
        },
        success (action) {
          // 存储当前点击id河类型
          sessionStorage.setItem('searchId', item.userId)
          // 打电话
          window.location.href = 'tel://' + item.mobile
          // 如果登录 增加一次拨打次数
          self.$router.push('/appraiseorder')
        },
        fail (action) {
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
.search-list {
  min-height: 3rem;
  position: relative;
}
.search-list-item {
  width: 100%;
  height: 1.56rem;
  padding: .2rem 0 .2rem .22rem;
  position: relative;
  background: @whiteColor;
  border-bottom: 1px solid @lineColor;
  .detail-cover {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 2;
    width: 1.6rem;
    height: 1.5rem;
  }
  .search-list-left {
    width: .8rem;
    height: 100%;
    float: left;
    .search-list-left__name {
      display: block;
      text-align: center;
      width: .6rem;
      height: .6rem;
      font-size: .32rem;
      line-height: .6rem;
      color: @whiteColor;
      background: @manColor;
      border-radius: 50%;
    }
    .search-list-left__img {
      display: block;
      width: .6rem;
      height: .6rem;
      background: transparent;
      border-radius: 50%;
    }
    .search-list-left__vip {
      display: block;
      // width: .6rem;
      text-align: left;
      padding-bottom: .04rem;
      margin-top: .14rem;
      font-size: .3rem;
      // font-style: italic;
      color: @midleColor;
      // border-bottom: 1px solid @lightColor;
    }
    .borderBRed {
      border-bottom: 1px solid @redColor !important;
    }
    &.woman {
      .search-list-left__name {
        background: @womanColor !important;
      }
    }
  }
  .search-list-line {
    float: left;
    width: 1px;
    height: .94rem;
    margin-top: .11rem;
    background: @lineColor;
  }
  .search-list-right {
    margin-left: 1rem;
    .search-list-right__top {
      font-size: .28rem;
      color: @defaultColor;
      height: .24rem;
      line-height: .24rem;
      .search-list-right__number {
        min-width: 2rem;
        height: .24rem;
        font-size: .32rem;
      }
      .search-list-right__times {
        width: 1rem;
        position: relative;
        color: @lightColor;
        margin-left: 1.3rem;
        .phone__icon {
          top: -.1rem;
          left:-.446rem;
        }
      }
      .search-list-right__percent {
        margin-left: .66rem;
        color: @lightColor;
        position: relative;
        .zan-icon {
          position: absolute;
          top: -.1rem;
          left:-.44rem;
          width: .44rem;
          height: .44rem;
          background: url(../../assets/img/zan@2x.png) no-repeat;
          background-size: .44rem .44rem;
        }
      }
    }
    .search-list-right__middle {
      width: 100%;
      font-size: .28rem;
      position: relative;
      height: .64rem;
      line-height: .64rem;
      .search-list-right__address {
        display: inline-block;
        width: 80%;
        height: .64rem;
        overflow: hidden;
        text-overflow: ellipsis;
        color: @lightColor;
      }
      .arrow__icon {
        right: .08rem;
        top: .1rem;
      }
    }
    .search-list-right__bottom {
      width: 100%;
      font-size: .28rem;
      padding-right: .42rem;
      height: .28rem;
      line-height: .28rem;
      .search-list-right__info {
        width: 4rem;
        height: .3rem;
        line-height: .32rem;
        overflow: hidden;
        color: @infoColor;
      }
      .search-list-right__range {
        color: @lightColor;
        display: inline-block;
        height: .32rem;
        line-height: .32rem;
        text-overflow: ellipsis;
        overflow: hidden;
        max-width: 1.8rem;
      }
    }
  }
}
</style>



