<template lang="html">
  <div class="pop-menu">
    <div class="mark" @click="close"></div>
    <div class="menu">
      <div class="hd">
        <i></i>
      </div>
      <ul class="list">
        <li class="item" @click="$router.push('/searchcontact')">
          <img src="../../assets/img/head/i01@3x.png" alt="">
          <span>新建联系人</span>
        </li>
        <li class="item" @click="clickItem('扫一扫')" v-if="false">
          <img src="../../assets/img/head/i02@3x.png" alt="">
          <span>扫一扫</span>
        </li>
        <li class="item" @click="clickItem('邀请好友')" v-if="false">
          <img src="../../assets/img/head/i03@3x.png" alt="">
          <span>邀请好友</span>
        </li>
        <li class="item" @click="clickItem('生成二维码')" v-if="false">
          <img src="../../assets/img/head/i04@3x.png" alt="">
          <span>生成二维码</span>
        </li>
        <li class="item" @click="$router.push('/friendcircle')">
          <img src="../../assets/img/head/i05@3x.png" alt="">
          <span>查看收藏</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    close () {
      this.$emit('colse-pop')
    },
    clickItem (item) {
      this.close()
      this.$emit('click-item', item)
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
 .pop-menu {
   .mark {
     position: fixed;
     left: 0;
     top: 1rem;
     right: 0;
     bottom: 1.12rem;
     background: rgba(0,0,0,0.3);
     z-index: 99;
   }
   .menu {
     position: absolute;
     z-index: 999;
     top: .5rem;
     left: 0;
     .hd {
       position: relative;
       height: 0.42rem;
       i {
         position: absolute;
         width: 0;
         top: .022rem;
         height: 0;
         border: 0.22rem solid transparent;
         display: inline-block;
         border-bottom-color: @whiteColor;
       }
     }
     .list {
       width: 2.3rem;
       background: @whiteColor;
       border-radius: .05rem;
       color:@defaultColor;
       font-size: .28rem;
       .item {
         width: 100%;
         border-bottom: 1px solid @lineColor;
         line-height: .88rem;
         padding-left: .12rem;
         img{
           width: .46rem;
           height: .46rem;
         }
         img, span {
           vertical-align: middle;
         }
       }
       .item:last-child {
         border-bottom:none
       }
     }
   }
 }
</style>



